<template>
  <main class="main">
    <h1>Despensa</h1>
    <ul class="productList">
      <ProductItem v-for='product in products' :key='product.id' :title='product.title' :weHave='product.weHave' :weNeed='product.weNeed' :comment='product.comment' :handleAdd="addItem.bind(this, product)" :handleRemove="removeItem.bind(this, product)"/>
    </ul>
  </main>
</template>

<script>
import ProductItem from '@/components/ProductItem.vue'
import Axios from 'axios';

export default {
  name: 'home',
  components: {
    ProductItem
  },
  mounted() {
    console.log();
    Axios(`${this.baseUrl}/products`).then(response => {
      // response.data.forEach(item => {
      //   item.image.url = `${baseUrl}${item.image.url}`
      // });
      this.$store.commit('writeProducts', response.data);
    });
  },
  computed: {
    products() {
      return this.$store.state.products;
    },
    baseUrl() {
      return this.$store.state.baseUrl;
    }
  },
  methods: {
    removeItem(product) {
      const data = {
        weHave: product.weHave + product.weNeed,
        weNeed: 0
      }
      Axios.put(`${this.baseUrl}/products/${product.id}`, data)
      .then(() => this.$router.go())
      .catch(error => console.log(error))
    },
    addItem(product) {
      const data = {
        weNeed: product.weNeed + 1
      }
      Axios.put(`${this.baseUrl}/products/${product.id}`, data)
      .then(() => this.$router.go())
      .catch(error => console.log(error))
    }
  }
}
</script>

<style scoped>
  .main {
    max-width: 800px;
    margin: 0 auto;
    padding: 0 20px;
  }
  .productList {
    list-style: none;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  }
</style>
