import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    baseUrl: process.env.NODE_ENV === 'production' ? 'https://despensa.azurewebsites.net/' : 'http://localhost:1337',
    products: []
  },
  mutations: {
    writeProducts(state, payload) {
      state.products = payload;
    }
  },
  actions: {

  }
})
